body {
  padding:0.5%;
  color:darkgrey;
  background-color:#14404a;
  }
  ul {
  list-style: none;
  }
  a {
  text-decoration: none;
  color:darkgrey;
  }
  a:hover {
  font-weight: bold;
  color: #f1b51c;
  }
  nav ul {
  background-color: rgb(0, 109, 139);
  margin: 0.5%;
  padding-inline-start: 0%;
  padding-top: 1%;
  padding-bottom: 1%;
  width:100%;
  }
  nav li {
  display:inline-block;
  margin: 0.5%;
  }
  nav a {
  text-decoration: none;
  font-size: larger;
  color:rgb(254, 255, 255);
  }
  